<template>
    <div>
        <Head title="登录/注册" :show="true" ></Head>
        <van-tabs v-model="active" class="login-box">
            <van-tab title="注册">
                <van-cell-group>
                    <van-field
                        v-model="register.username"
                        required
                        clearable
                        label="用户名"
                        right-icon="question-o"
                        placeholder="请输入用户名"
                        @click-right-icon="$toast('请输入4位长度的用户名')"
                    />
                    <van-field
                        v-model="register.mobile"
                        required
                        clearable
                        type="tel"
                        label="手机号"
                        placeholder="请输入手机号"
                        right-icon="question-o"
                        @click-right-icon="$toast('请输入11位长度的手机号')"
                    />
                    <van-field
                        v-model="register.password"
                        type="password"
                        label="密码"
                        placeholder="请输入密码"
                        required
                        right-icon="question-o"
                        @click-right-icon="$toast('请输入6-12位长度的密码')"
                    />
                    <van-field
                        v-model="register.dbpwd"
                        type="password"
                        required
                        clearable
                        label="确认密码"
                        placeholder="请输入确认密码"
                    />
                    <van-button @click="todoRegister" type="primary" size="large">立即注册</van-button>
                </van-cell-group>
            </van-tab>
            <van-tab title="登录">
                <van-cell-group>
                    <van-field
                        v-model="login.keys"
                        required
                        clearable
                        label="用户名/手机号"
                        placeholder="请输入用户名/手机号"
                    />
                    <van-field
                        v-model="login.password"
                        type="password"
                        label="密码"
                        placeholder="请输入密码"
                        required
                    />
                    <van-button @click="todoLogin" type="danger" size="large">马上登录</van-button>
                </van-cell-group>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
import {mapMutations} from "vuex"; 
export default {
    data(){
        return {
            active:1,
            register:{

            },
            login:{

            }
        }
    },
    
    methods:{
        ...mapMutations(['changeUserInfo']),
        todoLogin(){
            this.$axios.post("/vue/login",this.login).then(res=>{
                if(!!res.data.type){
                    sessionStorage.userInfo = JSON.stringify({
                        token:res.data.token,
                        username:this.login.keys
                    });

                    this.changeUserInfo({
                        token:res.data.token,
                        username:this.login.keys
                    });


                    setTimeout(()=>{
                        this.$router.push({name:'mine'});
                    },600);
                }else{
                    sessionStorage.userInfo = JSON.stringify({
                        token:""
                    });
                }
            })
        },
        todoRegister(){
            this.$axios.post("/vue/register",this.register).then(res=>{
                if(!!res.data.type){
                    this.active = 1;

                }else{
                    console.log("注册失败")
                }
                this.register = {};
            })
        }
    }
}
</script>

<style scoped>
.login-box{
    width:100%;
    padding:10px;
    box-sizing: border-box;
}
</style>
